.wrapper {
    height: 90vh;

    display: flex;
    justify-content: center;
    background-image: linear-gradient(rgba(4, 4, 4, 0.4), rgba(4, 4, 4, 0.4)),
        url(https://img5.goodfon.com/wallpaper/nbig/5/4c/vadim-sadovski-by-vadim-sadovski-science-fiction-visual-e-19.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.form {
    display: flex;
    flex-direction: column;
    width: auto;
    height: 480px;
    padding: 40px;
    border-radius: 15px 0px 15px 15px;
    background: rgba(0, 0, 0, 0.6);
    margin-top: 30px;
}

.label {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin-bottom: 40px;
    cursor: pointer;
}

.input {
    outline: none;
    font-size: 26px;
    font-weight: 500;
    line-height: 30px;
    padding: 10px 15px;
    min-width: 320px;
    max-width: 600px;
    padding: 15px 25px;
    color: #191d2b;
    border: 1px solid #191d2b;
    border-radius: 10px 0px 10px 10px;
}

.input::placeholder {
    color: #191d2b;
    font-size: 26px;
    font-weight: 500;
    line-height: 30px;
}

.labelText {
    color: aquamarine;
    font-size: 26px;
    font-weight: 500;
    padding-bottom: 10px;
}

.submitButton {
    cursor: pointer;
    padding: 15px 30px;
    color: aquamarine;
    font-size: 26px;
    font-weight: 600;
    padding-bottom: 10px;
    margin: 0 auto;
    border-radius: 10px 0 10px 10px;
    border: 1px solid aquamarine;
    transition: all 0.3s ease-in-out;
    background-color: transparent;
}

.submitButton:hover {
    font-size: 30px;
}
